import React, { Component } from "react";
import Comp2 from "./comp2";

export class comp1 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [],
      currentItem: {
        text: "",
        key: "",
        comp: false
      },
      count: 0
    };
    this.handleInput = this.handleInput.bind(this);
    this.addItem = this.addItem.bind(this);
    this.checkBoxChange = this.checkBoxChange.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
  }

  counts() {
    let countt = 0;

    const updatedCount = this.state.items.map(item => {
      if (item.comp === true) {
        countt = countt + 1;
      }
      console.log(countt)
    }
    )
    return (this.setState({ count: countt }))

  }

  handleInput(e) {
    this.setState({
      currentItem: {
        text: e.target.value,
        key: Date.now(),
        comp: false
      },
    });
  }

  addItem(e) {
    e.preventDefault();
    const newItem = this.state.currentItem;
    if (newItem.text !== "") {
      const Items = [...this.state.items, newItem];
      this.setState(
        {
          items: Items,
          currentItem: {
            text: "",
            key: "",
            comp: false
          },
        },
        () => {
          console.log(Items);
        }
      );
    }
  }

  checkBoxChange(key) {
    
    this.setState(prevState => {
      const updatedItems = prevState.items.map(item => {
        if (item.key === key) {
          return {
            ...item,
            comp: !item.comp
          }
        }
        return item
      })
      return {
        items: updatedItems
      }
    })
    // this.counts()
  }

  deleteItem(key) {
    const filterItems = this.state.items.filter(item =>
      item.key !== key);
    this.setState({
      items: filterItems
    })
    // this.counts()
  }

  componentDidMount() {
    this.interval = setInterval(() => this.counts(), 100);
  }
  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    return (
      <div className="container">
        <div className="head text-center">
          <form id="to-do-form" onSubmit={this.addItem}>
            <div className="input-group">
              <input
                className="form-control"
                type="text"
                placeholder="Enter the Task"
                value={this.state.currentItem.text}
                onChange={this.handleInput}
              />
              <div className="input-group-btn">
                <button className="btn btn-info" type="submit"> Add </button>
              </div>
            </div>
          </form>
        </div>
        <div>
          Total Tasks: {this.state.items.length}
          &emsp;&emsp;&emsp;&emsp;
          Completed Tasks: {this.state.count}
          &emsp;&emsp;&emsp;&emsp;
          Incomplete Taskd: {this.state.items.length - this.state.count}
        </div>
        <Comp2 items={this.state.items} checkBoxChange={this.checkBoxChange} deleteItem={this.deleteItem} />
      </div>
    );
  }
}

export default comp1;
